
footer {
  bottom: 0;
  background-color: #202229;
  padding: 5px 10px;
  position: absolute;
  width: calc(100% - 20px);
}
footer .container {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
footer a {
  color: #828a9f;
  display: inline-block;
  font-size: 14px;
  text-decoration: none;
  padding: 5px 0;
}
footer a:hover {
  text-decoration: underline;
}
footer .left,
footer .middle,
footer .right {
  width: 100%;
}
footer .middle {
  text-align: center;
}
footer .right {
  text-align: right;
}



