
html {
  height: 100%;
}
body {
  background-color: #262831;
  color: #cfd2da;
  font-family: 'Lato', 'Helvetica Neue', 'Ubuntu', 'Verdana', 'sans-serif';
  height: 100%;
  margin: 0;
}
#app {
  height: 100%;
}
#app > div {
  position: relative;
  padding: 50px 0;
  min-height: calc(100% - 100px);
}
#app > div > .container {
  min-height: 100%;
}
.container {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin-right: auto;
  margin-left: auto;
  overflow: auto;
  position: relative;
  padding: 0 15px;
}
@media screen and (min-width: 750px) {
.container {
    width: 750px;
}
}
@media screen and (min-width: 992px) {
.container {
    width: 970px;
}
}
@media screen and (min-width: 1200px) {
.container {
    width: 1170px;
}
}



