
.git-repo-growth {
  width: 25%;
  float: left;
  text-align: center;
  margin-bottom: 15px;
}
.git-repo-growth h3 {
  font-size: 28px;
  font-weight: 300;
  margin: 0;
}
.git-repo-growth span {
  color: #828a9f;
}
.git-repo-growth a {
  color: #828a9f;
  text-decoration: none;
}
.git-repo-growth a:hover {
  text-decoration: underline;
}
.git-repo-growth.vapor h3 {
  color: #696fc9;
}
.git-repo-growth.perfect h3 {
  color: #dda25d;
}
.git-repo-growth.kitura h3 {
  color: #24a8de;
}
.git-repo-growth.zewo h3 {
  color: #e64758;
}



