
.imprint .card {
  background-color: #ffffff;
  border-radius: 4px;
  -webkit-box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.14);
          box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.14);
  color: #262831;
  margin: 0 auto;
  padding: 30px;
  text-align: center;
  width: 290px;
}
.imprint .card p {
  font-size: 14px;
  margin: 0 0 5px;
}
.imprint .card a {
  color: #262831;
  text-decoration: none;
}
.imprint .card a:hover {
  text-decoration: underline;
}



