
.list-card {
  margin-bottom: 30px;
}
.list-card h2 {
  text-align: center;
  margin-bottom: 10px;
  font-weight: 500;
  font-size: 18px;
}
.list-card .card {
  background-color: #ffffff;
  border-radius: 4px;
  -webkit-box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.14);
          box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.14);
  margin: 0 auto;
  padding: 30px;
  width: 290px;
}
.list-card .card ul {
  margin: 0;
  padding-left: 15px;
}
.list-card .card ul li {
  color: #262831;
  font-size: 14px;
  margin-top: 5px;
}



